body{
    margin: 0;
    background-color:#056679;
    font-family: sans-serif;

}
.todo-container{
    
    width: 350px;
    text-align: center;
    margin: auto;
    margin-top: 30px;
}
.input-section{
    background-color: #055565;
    padding: 18px;
    border-radius: 10px;
    box-shadow: 0 4px 6px #00000066;
}

h1{
    color: #08869e;
}

.input-section input{
    border-radius: 5px;
    border: 1px solid silver;
    width: 100%;
    padding: 10px;
    box-sizing: border-box;
    background-color: #056679;
    outline: none;
    color: #4abfd9;
}

input::placeholder{
    color: #055565;
}

ul{
  
    padding: 0;
    text-align: left;

}

ul li{
    list-style: none;
    background-color: #055565;
    padding: 15px;
    border-radius: 5px;
    margin-bottom: 10px;
    color: #08869e;
    box-shadow: 0 4px 6px #00000066;
    display: flex;
}

ul li i{
    text-align:right ;
    flex: 1;
    cursor: pointer;
}